import React from 'react';

interface AvatarProps {
  imageUrl: string;
  altText?: string;
  size?: number;
}

const Avatar: React.FC<AvatarProps> = ({ imageUrl, altText = 'Avatar', size = 50 }) => {
  return (
    <img
      src={imageUrl}
      alt={altText}
      style={{
        width: size,
        height: size,
        borderRadius: '50%',
      }}
    />
  );
};

export default Avatar;
